import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import { Empty, Collapse, Select, Row, Col, Divider, DatePicker } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/balanceSheetProjectReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const xx = 15;


class BalanceSheetProjectReport extends Component {
  state = {
    filter: {
      transaction_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_project: null,
      id_location: null
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getBalanceSheetProjectReports } = this.props;
    getBalanceSheetProjectReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.balanceSheetProjectReport.error) {
      notification("error", this.props.balanceSheetProjectReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.balanceSheetProjectReport.success) {
      notification("success", this.props.balanceSheetProjectReport.success);
      this.props.clearSuccess();
    }
  }

  // Fungsi marakke mumet ndase 
  hitungTotalAmountParent = (array_ori, array_filter) => {
    //console.log(array_filter)
    array_filter.map((data) => {
      if(data.has_child === 1){
        let filter = array_ori.filter((x) => x.id_account_parent === data.id_account)
        if(filter.length > 0){
          this.hitungTotalAmountParent(array_ori, filter)
        }
      }
      else if(data.id_account_parent === null){
        console.log("Ya")
      }
      else{

        let parent = array_ori.find((x) => x.id_account === data.id_account_parent)
        array_ori.map((x) => {
          
          if(x.id_account === parent.id_account){
            let parent2 = parent
            while(parent2!==undefined){
              parent2.amount += parseFloat(data.amount)
              parent2 = array_ori.find((y) => y.id_account === parent2.id_account_parent)
              //console.log(parent2)
            } 
          }
        })
      }
    })   
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } 
    else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    }
    else if (name === "transaction_date") {
      //console.log(value)
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value.format("YYYY-MM-DD HH:mm:ss")
        }
      });
    }
    else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_project) {
      string += "id_project=" + filter.id_project;
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.transaction_date) {
      string
        ? (string +=
            "&transaction_date=" + filter.transaction_date)
        : (string +=
            "transaction_date=" + filter.transaction_date);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getBalanceSheetProjectReports } = this.props;
    //const { current_page } = this.props.balanceSheetProjectReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getBalanceSheetProjectReports(1, null, filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getBalanceSheetProjectReports } = this.props;
    this.setState({
      filter: {
        transaction_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_project: null,
        id_location: null
      }
    });
    getBalanceSheetProjectReports();
  };

  // Definisi kolom2 serta field2 API yang dipakai tabel
  columns = [
    {
      title: "Account Name",
      dataIndex: "account_name",
      rowKey: "account_name",
      width: "40%",
      render: (text, row) => {
        if (row.has_child) {
          if (row.level > 1) {
            return (
              <strong style={{ marginLeft: `${row.level * xx}px` }}>
                {text}
              </strong>
            );
          } else {
            return <strong>{text}</strong>;
          }
        }
        else if (row.level === 1) {
          return text;
        }
        else {
          return (
            <span style={{ marginLeft: `${row.level * xx}px` }}>{text}</span>
          );
        }        
      }
    },
    {
      title: "Amount (IDR)",
      dataIndex: "amount",
      rowKey: "amount",
      align: "right",
      width: "30%",
      render: (text, row) => {
        return row.has_child === 1
                ?    
                  !row.account_name.includes("Total") && 
                  row.level === 1 
                    ? ""
                    :
                      (
                        <strong>
                          <NumberFormat value={parseFloat(text)} />
                        </strong>
                      )
                : 
                  <NumberFormat value={parseFloat(text)} />
          
      }
    },
  ]

  render() {
    // console.log(this.props.balanceSheetProjectReport);
    // Data setup
    const { balance_sheet_project_reports, projects, loading, loadingForeign, locations, filter } = 
      this.props.balanceSheetProjectReport ? this.props.balanceSheetProjectReport: [];

    let balance_sheets = balance_sheet_project_reports

    balance_sheets.map((data, key)  => {
      if(data.has_child === 1){
        data.amount = 0
      } 
      // else{
      //   data.amount = 1
      // }
    })

    if(balance_sheets.length > 0){
      let level_1 = balance_sheet_project_reports.filter((x)  => x.level === 1)
      this.hitungTotalAmountParent(balance_sheets, level_1);
    }
    

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let total_asset = {
      account_name : "Total Asset",
      has_child:1,
      level :1,
      amount: 0
    };
    let total_liabilities = {
      account_name : "Total Liabilities",
      has_child:1,
      level :1,
      amount: 0
    };
    let total_equity = {
      account_name : "Total Equity",
      has_child:1,
      level :1,
      amount: 0
    };
    let total_liabilities_equity = {
      account_name : "Total Liabilities & Equity",
      has_child:1,
      level :1,
      amount: 0
    };
    let array_asset = [];
    let array_liabilities = [];
    let array_equity = [];

    balance_sheets.map((data) => {
      if(data.gl_type === 1){
        if(data.level === 1){
          total_asset.amount = data.amount
        }
        array_asset.push(data);
      }
      else if(data.gl_type === 2){
        if(data.level === 1){
          total_liabilities.amount = data.amount
        }
        array_liabilities.push(data);
      }
      else if(data.gl_type === 3){
        if(data.level === 1){
          total_equity.amount = data.amount
        }
        array_equity.push(data)
      }
    })

    let dataSource = []

    if(balance_sheet_project_reports.length > 0){
      total_liabilities_equity.amount = total_equity.amount + total_liabilities.amount;

      dataSource = [...array_asset]
      dataSource.push(total_asset);
      dataSource = [...dataSource, ...array_liabilities]
      dataSource.push(total_liabilities);
      dataSource = [...dataSource, ...array_equity]
      dataSource.push(total_equity)
      dataSource.push(total_liabilities_equity);
    }

    // console.log(total_asset)

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="balanceSheetProjectReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="balanceSheetProjectReports.pageHeader" />}
              columns={this.columns}
              data={dataSource}
              filter={this.state.filter}
              projects={projects}
              lo
              filterDate="transaction_date"
              dateFormat="DD MMMM YYYY HH:mm:ss"
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
            />

            <Button style={{ marginLeft:"5px" }} onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="projects.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_project"
                      )}
                      value={
                        this.state.filter.id_project
                          ? this.state.filter.id_project
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="select.project" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      {projects.map((data, key) => (
                        <Option key={key} value={data.id_project}>
                          {`${data.project_code} (${data.project_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="asOf" />
                      </label>
                      <br />
                      <DatePicker
                        style={{ width: "80%" }}
                        showTime
                        format="DD MMMM YYYY HH:mm:ss"
                        onChange={this.handleFilterChange.bind(
                          this,
                          "transaction_date"
                        )}
                        value={
                          this.state.filter.transaction_date
                            ? moment(this.state.filter.transaction_date)
                            : moment()
                        }
                        allowClear={false}
                        // disabledDate={e => e.valueOf() > moment()}
                      />   
                    </Col>
                    <Col span={12}
                      style={{ marginTop:"24px" }}
                    >
                      <Button
                        size="small"
                        type="primary"
                        style={{ marginRight: "10px" }}
                        onClick={this.handleFilter}
                        disabled={!this.state.filter.id_project}
                      >
                        <IntlMessages id="filter.only" />
                      </Button>
                      <Button onClick={this.handleResetFilter} size="small">
                        <IntlMessages id="filter.reset" />
                      </Button>
                    </Col>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            { 
              !filter  
              ? (<Empty description={<IntlMessages id="noProject" />} />) 
              :  
                !filter.includes("id_project=") 
                  ? (<Empty description={<IntlMessages id="noProject" />} />)  
                  :
                    balance_sheet_project_reports.length === 0 &&
                    !this.props.balanceSheetProjectReport.loading ? (
                      <Empty
                        description={
                          <IntlMessages id="balanceSheetProjectReports.table.noData" />
                        }
                      />
                    ) : (
                      <div className="isoInvoiceTable">
                        <Divider orientation="left" style={{ fontSize: 14 }}>
                          <IntlMessages id="details" />
                        </Divider>
                        <Scrollbars style={{ width: "100%" }}>
                          <TableWrapper
                            dataSource={dataSource}
                            rowKey={record => record.id_account}
                            columns={this.columns}
                            loading={loading}
                            className="invoiceListTable"
                            bordered={true}
                            // Set pagination, jika ada page_size mengunakan pagination API
                            // jika tidak ada page_size menggunakan pagination bawaan
                            pagination={false}
                          />
                        </Scrollbars>
                      </div>
                    )
            }
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    balanceSheetProjectReport: state.balanceSheetProjectReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(BalanceSheetProjectReport);
